
.fc{display: flex;flex-direction: column; }
.fr{display: flex;flex-direction: row;}


.contentb{display: flex;flex-wrap: wrap;justify-content: center;align-items: center;font-family: "Times New Roman"}

.contentb .box{max-width: 20em; margin: 1em 0px 1em 1em;border-top:0.3em solid red;padding: 1em}

.contentb .box:hover{box-shadow: 1px 1px 1px 1px violet;border: 0;cursor: pointer;border-radius: 5px}

.contentb img{width: 94%;height: 5em;,margin-top: 2em}
.storyp{margin-bottom:5em}
.story-topic{font-weight: 900;font-size: .8em;}
.story-subject{font-size: 2em;color:#1affa3}
.story-content{line-height: 1.3em;max-height: 6em;overflow-y: hidden;}
.add-button{font-size: 2.8em;padding: 0.2em;border-radius: 50%;background-color: rgba(172,7,90,.8);position: fixed;bottom: 1.4em;right: 1em;
	font-weight: 600;color: #1affa3}
.add-button i{ color: #1affa3}
.cancel-button{font-size: 2.2em;padding: 0.2em;border-radius: 50%;background-color: rgba(172,7,90,.8);position:absolute;top: .4em;left:.4em;
	font-weight: 600;color: #1affa3;}
.blog-form{height: 96vh; background-color: rgba(45,33,45,.82);width: 50%;position: fixed; top: 10px;right: 0px;border-radius: 5px;
	box-shadow:1px 1px 1px 1px grey;justify-content: center;display: flex;overflow-y:  scroll;}
.blog-form-box{margin-top: 3.5em;width: 94%;font-family: "Lucida Console", "Courier New", monospace;font-size: 1.2em;height: 80vh;
position: absolute; left: 10px}
.mobile-text-filed{display: none;visibility: hidden;}


.tag-span{	padding-left: 20px;color: orange;position: absolute;top: 2em;right: .6em;display: inline-block;width: 85%}

.sub-butt{outline: none;background-color:transparent;border: 1px solid #1affa3;height: 8vh;float: right;
	margin: 0.6vw;color: #1affa3;transition: background-color 2s, height 4s;border-radius: 4px;padding: 20px}

.sub-butt span,.sub-butt i {color:  #1affa3;float: left;padding-left: 12px}
.sub-butt i {float: right;}
.sub-butt:hover * {color:rgb(50,50,50)}
.sub-butt:hover {background-color:#1affa3}
.views-eye {color:rgba(102,193,190,.7);padding: 3px}
.blog-form-box div {display: block;}
.fr div{width: 50%;display: inline-flex;}
.frck textarea{height: 700px;background-color: red}


@media only screen and (max-width: 768px) {
	.blog-form{width: 90vw}
	.tag-span{right: 4.6em;width: 70%}
	.contentb .box{max-width: 80%;}
}