.app{background-color: red}
.page-loader{background-color:#404040;z-index: 2;position: absolute;left: 0;height: 100vh;width: 100%;top:0;display:flex;
    justify-content: center;align-items: center;flex-direction: column;animation: pageloder 2s linear 0s infinite alternate;}
.page-loader    .divyname{background-color:white;color: #d9d9d9;padding: .6em;margin-bottom:  1.3em;border-radius: .5em}
.loader-image{height: 60px}
body{background-color: #404040}

/* width */
::-webkit-scrollbar { width: .09em; height: .06em }

/* Track */
::-webkit-scrollbar-track { box-shadow: inset 0 0 5px grey;  border-radius: 10px;}
 
/* Handle */
::-webkit-scrollbar-thumb { background: #d9d9d9;   border-radius: 10px;}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {  background: #b30000; }

*,ul, ul a,p,span{padding: 0px;margin: 0px;color:#d9d9d9;text-decoration: none;line-height: 0.9;font-size: 1.0em;}
a:link,a a hover{text-decoration: none;cursor: pointer;}
.fc{display: flex;flex-direction: column;align-items: center;justify-content: }
.fr{display: flex;flex-direction: row;align-items:center;justify-content: center;}
button{background-color: black}
.linktag img{background-color:#d9d9d9;border-radius: 4px;margin-bottom: 10px}
.appnin{background-color: #404040;}
.navb{height:100%;position: fixed;top: 0;left: 0; background-color: #1a1a1a;padding: 0;width: 130px;z-index: 3;overflow-y:scroll;}
.content{background-color: #404040; margin-left:130px;padding:0px 0px 0px 0px;margin-bottom: 30px}
.linktag b{font-size: 	2.6vh}

.linktag{background-color: black;color: white;width: 100%;padding: 2vw;padding-top: 20px}
.linktag span{text-align: center;font-size: 1.1em}
.navb ul a:hover,.navb ul a i:hover{color:#1affa3}
.navb ul div a{margin: 3px}



.navb ul>a,.navb ul div {height: 45px;text-align: center;border-top: 1px solid gray;width: 	100%;display: flex;justify-content: center;
	align-items: center;}
.navb ul {padding-top: 50px;width: 	100%}
.content{}
.navb ul div{height: 70px;justify-content: center;align-items: flex-end;}
.txt-v{writing-mode: vertical-rl; }
.content .dl{position: fixed;bottom: 5vh;left: 0px}
.content .dr{position: fixed;bottom: 5vh;right: 0px;}
.name-box{margin: 25px;}
.name-box p, .name-box img{font-size: 3.6em;font-weight: 700; line-height: 150%;}
.name-box p{width:110vw;overflow: auto;}
.name-box img{margin-bottom: 1px}
.contact-box-address{ font-size: 1.2em;margin:20px;letter-spacing: 0.15vw}
.contact-box { border:1px solid #1affa3;padding: .3em;width:25%;text-align: center; transition: background 2s, color 4s; color:#1affa3;margin: 1em;
                font-size: 1.7em}
.contact-box:hover {background-color: #1affa3;color: rgba(50,50,50,10);cursor: pointer;  }
.open-close{position: fixed;top: 3vw;right:3vw;font-size: 2.6em;padding: .26em;border-radius: 50%;align-items: center;justify-content: center;
		box-shadow: 1px 1px 3px 2px rgba(100,180,100,.8) ;cursor: pointer;display: none;z-index: 3;background-color: #404040}
.open-close-baby{color:rgba(100,140,100,.6);font-weight: 100;}

.name-box{
  overflow: hidden;
  animation: example 8s linear 0s infinite alternate;
}
.foot-note{position:fixed;bottom: 0px}

.dl,.dr{
	animation: scroll 5s linear 0s infinite alternate;
}


@keyframes example {
  0%   {height: 40px;width: 0}
  100% {height: 60vh;width: 100%}
}
@keyframes scroll {
  0%   {padding-bottom: 0px}
  100% {transform: translate(0px,-70px);color: orange}
}

@media only screen and (max-width: 768px) {
  /* For desktop: */
  .navb{width: 100%;display: none;}
  .open-close{display:flex;}
  .content{margin-left: 0px;padding: 10px}
  .appnin{display: flex;flex-direction: column;}
  .linktag{padding-top: 40px}
}