
.fc{display: flex;flex-direction: column;align-items: center;justify-content: }
.fr{display: flex;flex-direction: row;align-items:center;justify-content: center;}


.contactteli{width: 100%;top: 0px;right: 0px;display: flex;align-items: flex-start;justify-content: flex-start;}
.contactteli div{width: 50%;}
.contactteli-form-box {align-items: flex-start;padding-top: 5vw}
.contactteli-form-box p{color: #1affa3;font-size: 3em;font-weight: 400;margin: 0.6vw;}
.contactteli .formbox,.contactteli form{padding:0vw;width: 100%}
.contactteli form div{width: 100%}
.contactteli form div button {outline: none;background-color:transparent;border: 1px solid #1affa3;height: 8vh;width: 40%;float: right;
	margin: 0.6vw;color: #1affa3;transition: background-color 2s, height 4s;border-radius: 4px;padding: 20px}
.contactteli form div button span,.contactteli form div button i {color:  #1affa3;float: left;}
.contactteli form div button i {float: right;}
.contactteli form div button:hover * {color:rgb(50,50,50)}
.contactteli form div button:hover {background-color:#1affa3}
.contactteli-form-box .contacsodiq {font-size: 1.1em; font-weight: 500;color: orange;font-family: "Lucida Console", "Courier New"}
.foot-tag i {color: grey;font-family: "Lucida Console", "Courier New"}


@media only screen and (max-width: 768px) {
  .contactteli-form-box{width: 100%}
  .contactteli div{width: 100%;position: 0px}
  .contactteli{flex-direction: column;padding: 10px}
  .skill-dom{width: 100%;position: relative;height: 80vh;align-items: flex-start; justify-content: flex-start;}
  .skills-set h5{padding: 1.4vw;margin: 1em;padding: 1.1em}
}