

.fc{display: flex;flex-direction: column;align-items: center;justify-content: }
.fr{display: flex;flex-direction: row;align-items:center;justify-content: center;}

.myself{width: 50%;position: relative;margin: 10px;font-family: Arial, Helvetica, sans-serif;font-family: "Lucida Console", "Courier New", monospace;}
.myself h2{font-size: 1.4}
.myself .pan{animation: examplei 10s linear 0s infinite alternate;height: 35vh}
.myself .pan:hover {
  animation-play-state: paused;
}
.skill-dom{width: 50%;position: relative;height: 85vh;align-items: flex-start; justify-content: center;
	animation: exampleiii 1s linear 2s infinite alternate;align-content: stretch;padding-right: 10px;}
.myself .me{color:#1affa3;font-weight: 900;font-family: Georgia, serif;
  font-size: 4em;width: 100%  }

@keyframes examplei {
  0%   {font-size: .8em;color: #1affa3}
  50% {font-size: 1.0em;color:rgb(200,200,200)}
   100%   {font-size: 1.2em;color:violet;}
}
.contacti{width: 100%;bottom: 2px;color: #1affa3;font-size:1em;position:relative;margin-top: 1em}
.aa i{color: rgb(50,250,200);margin-top: 6px;display: inline-block;}
.aa:hover{text-decoration: inherit;background-color: rgba(100,100,100,4);padding: 5px;border-radius: 3px}






.skill-dom .panu {color:#1affa3;position: absolute;left: 0px; animation: exampleu 3s linear 0s infinite alternate;font-size: 0.9em;font-weight: 600}
.skill-dom .pand {color:#1affa3;position:absolute;left: 0px; animation: exampled 3s linear 0s infinite alternate;font-size: 0.9em;font-weight: 600}
.skill-dom .panur {color:#1affa3;position:absolute;top:10px; animation: exampleur 3s linear 0s infinite alternate;font-size: 0.9em;font-weight: 600}
.skill-dom .pandr {color:#1affa3;position:absolute;bottom:0px; animation: exampledr 3s linear 0s infinite alternate;font-size: 0.9em;font-weight: 600}


/*.skill-dom{position: relative;}*/
.film {width:25%;height: 20px; position: relative;}

@keyframes exampleu {
	0%   { bottom:-70vh;}
	50%{-ms-transform: rotate(20deg); transform: rotate(90deg);}
	100% {bottom:0vh;}
}
@keyframes exampled {
		0%   { top:0px;display: block;}
		50%{-ms-transform: rotate(90deg);transform: rotate(90deg);right: 0px}
  100% {top:70vh;display:none}
}

@keyframes exampleur {
	0% { left:-10vw;}
	50%{-ms-transform: rotate(20deg); transform: rotate(360deg);left: 20vw}
	100% {left:30vw;transform:  rotate(360deg);}
}
@keyframes exampledr {
	0% { left:-10vw;}
	50%{-ms-transform: rotate(20deg); transform: rotate(360deg);left: 20vw}
	100% {left:30vw;transform:  rotate(360deg);}
}

@media only screen and (max-width: 768px) {
  .myself{width: 100%}
  .contenti{flex-direction: column;padding: 10px}
  .skill-dom{width: 100%;position: relative;height: 80vh;align-items: flex-start; justify-content: flex-start;margin-top: 2em}
}